idden;\n}\n\n.ag-filter-condition {\n    display: flex;\n}\n\n/**\n ****************************\n * Floating Filter\n ****************************\n*/\n\n.ag-floating-filter-body {\n    position: relative;\n    display: flex;\n    flex: 1 1 auto;\n    height: 100%;\n}\n\n.ag-floating-filter-full-body {\n    display: flex;\n    flex: 1 1 auto;\n    height: 100%;\n    width: 100%;\n    align-items: center;\n    overflow: hidden;\n}\n\n.ag-floating-filter-full-body > div {\n    flex: 1 1 auto;\n}\n\n.ag-floating-filter {\n    display: inline-block;\n    position: absolute;\n}\n\n.ag-floating-filter-input {\n    align-items: center;\n    display: flex;\n    width: 100%;\n\n    > * {\n        flex: 1 1 auto;\n    }\n}\n\n.ag-floating-filter-button {\n    display: flex;\n    flex: none;\n}\n\n/**\n ****************************\n * Drag & Drop\n ****************************\n*/\n\n.ag-dnd-ghost {\n    position: absolute;\n    display: inline-flex;\n    align-items: center;\n    cursor: move;\n    white-space: nowrap;\n}\n\n/**\n ****************************\n * Overlay\n ****************************\n*/\n.ag-overlay {\n    height: 100%;\n    left: 0;\n    pointer-events: none;\n    position: absolute;\n    top: 0;\n    width: 100%;\n}\n\n.ag-overlay-panel {\n    display: flex;\n    height: 100%;\n    width: 100%;\n}\n\n.ag-overlay-wrapper {\n    display: flex;\n    flex: none;\n    width: 100%;\n    height: 100%;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n}\n\n.ag-overlay-loading-wrapper {\n    // prevent interaction with grid while it's loading\n    pointer-events: all;\n}\n\n/**\n ****************************\n * Popup\n ****************************\n*/\n.ag-popup-child {\n    z-index: 5;\n}\n\n.ag-popup-editor {\n    position: absolute;\n    user-select: none;\n    z-index: 1;\n}\n\n.ag-large-text-input {\n    display: block;\n}\n\n/**\n ****************************\n * Virtual Lists\n ****************************\n*/\n.ag-virtual