\n    }\n    &.ag-resizer-topRight {\n        top: 0;\n        right: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nesw-resize;\n    }\n    &.ag-resizer-right {\n        top: 5px;\n        right: 0;\n        bottom: 5px;\n        width: 5px;\n        cursor: ew-resize;\n    }\n    &.ag-resizer-bottomRight {\n        bottom: 0;\n        right: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nwse-resize;\n    }\n    &.ag-resizer-bottom {\n        bottom: 0;\n        left: 5px;\n        right: 5px;\n        height: 5px;\n        cursor: ns-resize;\n    }\n    &.ag-resizer-bottomLeft {\n        bottom: 0;\n        left: 0;\n        height: 5px;\n        width: 5px;\n        cursor: nesw-resize;\n    }\n    &.ag-resizer-left {\n        left: 0;\n        top: 5px;\n        bottom: 5px;\n        width: 5px;\n        cursor: ew-resize;\n    }\n}\n\n/**\n ****************************\n * Tooltip\n ****************************\n*/\n\n.ag-tooltip {\n    position: absolute;\n    pointer-events: none;\n    z-index: 99999;\n}\n\n/**\n ****************************\n * Animations\n ****************************\n*/\n\n// this is used by the animateShowChangeCellRenderer. it is arguable that this belongs in the themes,\n// however it is not tied to color, only placement and visiblity, which is behaviour and not style,\n// thus belongs here, besides it doesn't change wih the themes\n.ag-value-slide-out {\n    margin-right: 5px;\n    opacity: 1;\n    transition: opacity 3s, margin-right 3s; // as value fades, it also moves to the left via the margin setting\n    transition-timing-function: linear;\n}\n\n.ag-value-slide-out-end {\n    margin-right: 10px;\n    opacity: 0;\n}\n\n.ag-opacity-zero {\n    opacity: 0 !important;\n}\n\n\n/**\n ****************************\n * Menu\n ****************************\n*/\n.ag-menu {\n    max-height: 100%;\n    overflow-y: auto;\n    position: absolute;\n    user-select: none;\n}\n\n.ag-menu-column-select-wrapper {\n 